
.weather-container {
    width: 100%;
    height: 100%;
}
.weather-attrib {
    background-color: orange;
}

.weather-attrib#temperature {
    width: 100%;
    padding-top: 5%;
    
    text-align: center;
    font-size: 10vw;
}
.weather-attrib#details {
    text-align: center;
    font-size: 2vw;
}

@-webkit-keyframes icon-motion {
    0% {left: 15%;}
    50% {left: 35%;}
    100% {left: 15%;}
}

@keyframes icon-motion {
    0% {left: 15%;}
    50% {left: 35%;}
    100% {left: 15%;}
}
.weather-icon img{
    position: relative;
    
    width: 50%;
    height: 50%;
    
    -webkit-filter: invert(100%);
    
            filter: invert(100%);
    -webkit-animation-name: icon-motion;
            animation-name: icon-motion;
    -webkit-animation-duration: 5s;
            animation-duration: 5s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}
@media screen and (max-width: 800px) {
    .weather-attrib#temperature {
        font-size: 20vw;
    }
    .weather-attrib#details {
        font-size: 5vw;
    }
}
@media screen and (min-width: 1600px) {
    .weather-attrib#temperature {
        font-size: 10em;
    }
}
.time-container {
    width: 100%;
    height: 100%;
    text-align: center;
}
.display-container {
    width: 100%;
    color: white;
    margin-top: 10vw;
}
.time-display {
    background-color: var(--light-background-color);
    font-size: 15vw;
}
.date-display {
    background-color: var(--highlight-background-color);
    font-size: 5vw;
}
@media screen and (max-width: 800px) {
    .display-container {
        margin-top: 0;
    }
    .time-display {
        font-size: 20vw;
    }
}
@media screen and (min-width: 1800px) {
    .time-display {
        font-size: 16em;
    }
    .date-display {
        font-size: 4em;
    }
}
:root {
    --menu-line-spacing: 6px;
}

.sbcont {
    position: relative;
    display: inline-block;
    cursor: pointer;
    margin: 15px;
    padding: 10px;
    background-color: var(--highlight-background-color);
}
.sbcont:hover .bar1 {
    -webkit-transform: rotateZ(90deg) translate(11px, 15px);
            transform: rotateZ(90deg) translate(11px, 15px);
}
.sbcont:hover .bar2 {
    -webkit-transform: rotateZ(90deg);
            transform: rotateZ(90deg);
}
.sbcont:hover .bar3 {
    -webkit-transform: rotateZ(90deg) translate(-11px, -15px);
            transform: rotateZ(90deg) translate(-11px, -15px);
}
.sbcont a {
    margin: 0;
    text-decoration: none;
}

.bar1, .bar2, .bar3 {
    background-color: white;
    width: 35px;
    height: 5px;
    transition: 0.3s;
}
.bar1 {
    margin-top: 0px;
    margin-bottom: 6px;
    margin-bottom: var(--menu-line-spacing);
}
.bar2 {
    margin-top: 6px;
    margin-top: var(--menu-line-spacing);
    margin-bottom: 6px;
    margin-bottom: var(--menu-line-spacing);
}
.bar3 {
    margin-top: 6px;
    margin-top: var(--menu-line-spacing);
    margin-bottom: 0px;
}
:root {
    --background-color: #1f1f1f;
    --light-background-color: #333333;
    --highlight-background-color: #3f3f3f;

    --content-height: 80%;
    --settings-height: 20%;

    --time-panel-width: 60%;
    --weather-panel-width: 40%;
}

html, body, div#root{
    margin: 0;
    background-color: #1f1f1f;
    background-color: var(--background-color);
    height: 100%;
    width: 100%;
    font-family: Arial, Helvetica, sans-serif;
}
/* container for content and settings */
.home {}

.content {
    position: relative;
    height: 80%;
    height: var(--content-height);
    width: 100%;
}
.content-column {
    float: left;
}
.content-column#time {
    width: 60%;
    width: var(--time-panel-width);
}
.content-column#weather {
    width: 40%;
    width: var(--weather-panel-width);
}
.content:after {
    content: "";
    display: block;
    clear: both;
}
@media screen and (max-width: 800px) {
    .content-column#time, .content-column#weather{
      width: 100%;
    }
}
.settings {
    position: relative;
    height: 20%;
    height: var(--settings-height);
    width: 10%;
    min-width: 100px;
}
